<template>
  <div class="orders-panel">
    <div class="panel-header">
      <h2>Orders Management</h2>
      <div class="status-filters">
        <button 
          v-for="status in ['all', 'pending', 'approved', 'rejected']"
          :key="status"
          :class="{ active: currentStatus === status }"
          @click="currentStatus = status"
        >
          {{ status.charAt(0).toUpperCase() + status.slice(1) }}
        </button>
      </div>
    </div>

    <div class="orders-list">
      <div v-for="order in filteredOrders" :key="order.id" class="order-card">
        <div class="order-header">
          <div>
            <h3>Order #{{ order.id.slice(0,8) }}</h3>
            <p>{{ new Date(order.created_at).toLocaleString() }}</p>
          </div>
          <span :class="['status-badge', order.status]">{{ order.status }}</span>
        </div>

        <div class="order-details">
          <div class="items-summary">
            <h4>Items ({{ order.items.length }})</h4>
            <ul class="items-list">
              <li v-for="item in order.items" :key="item.id" class="item-row">
                <div class="item-info">
                  <img :src="item.image_url" :alt="item.name" class="item-thumbnail">
                  <div>
                    <strong>{{ item.name }}</strong>
                    <span class="item-quantity">x{{ item.quantity }}</span>
                  </div>
                </div>
                <div class="item-price">€{{ (item.price * item.quantity).toFixed(2) }}</div>
              </li>
            </ul>
            <div class="order-totals">
              <div class="total-row">
                <span>Subtotal:</span>
                <span>€{{ calculateSubtotal(order).toFixed(2) }}</span>
              </div>
              <div class="total-row">
                <span>Shipping:</span>
                <span>Free</span>
              </div>
              <div class="total-row grand-total">
                <span>Total:</span>
                <span>€{{ order.total_amount.toFixed(2) }}</span>
              </div>
            </div>
          </div>

          <div class="order-info">
            <div class="shipping-info">
              <h4>Shipping Details</h4>
              <p>{{ order.shipping_address.address }}</p>
              <p>{{ order.shipping_address.postalCode }} {{ order.shipping_address.city }}</p>
              <p>{{ order.shipping_address.phone }}</p>
            </div>
            
            <div class="payment-info">
              <h4>Payment Details</h4>
              <p><strong>Method:</strong> {{ order.payment_method }}</p>
              <p><strong>Status:</strong> {{ order.status }}</p>
              <p v-if="order.stripe_session_id">
                <strong>Stripe Session:</strong> 
                <code>{{ order.stripe_session_id }}</code>
              </p>
              <p><strong>Order Date:</strong> {{ formatDate(order.created_at) }}</p>
            </div>
          </div>

          <div class="order-actions" v-if="order.status === 'pending'">
            <button class="approve-btn" @click="showApprovalModal(order)">
              <span class="material-symbols-rounded">check_circle</span>
              Approve
            </button>
            <button class="reject-btn" @click="updateOrderStatus(order.id, 'rejected')">
              <span class="material-symbols-rounded">block</span>
              Reject
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Add Approval Modal -->
  <Modal v-if="showModal" @close="showModal = false">
    <template #header>
      <h2>Approve Order</h2>
    </template>
    <template #default>
      <div class="approval-options">
        <p>Choose how you want to approve this order:</p>
        
        <button class="approval-option" @click="approveWithPayment">
          <span class="material-symbols-rounded">payments</span>
          <div>
            <h3>Approve with Payment URL</h3>
            <p>Generate a Stripe payment link and copy to clipboard</p>
          </div>
        </button>

        <button class="approval-option" @click="approveWithoutPayment">
          <span class="material-symbols-rounded">done_all</span>
          <div>
            <h3>Approve without Payment URL</h3>
            <p>Mark as approved without generating payment link</p>
          </div>
        </button>

        <div class="modal-actions">
          <button class="cancel-btn" @click="showModal = false">Cancel</button>
        </div>
      </div>
    </template>
  </Modal>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import { loadStripe } from '@stripe/stripe-js'
import Modal from '@/components/Modal.vue'

export default {
  name: 'OrderManager',
  components: {
    Modal
  },
  setup() {
    const orders = ref([])
    const currentStatus = ref('all')
    const showModal = ref(false)
    const selectedOrder = ref(null)

    const filteredOrders = computed(() => {
      if (currentStatus.value === 'all') return orders.value
      return orders.value.filter(order => order.status === currentStatus.value)
    })

    async function fetchOrders() {
      const { data, error } = await supabase
        .from('orders')
        .select('*')
        .order('created_at', { ascending: false })

      if (error) {
        console.error('Error fetching orders:', error)
        return
      }

      orders.value = data
    }

    function showApprovalModal(order) {
      selectedOrder.value = order
      showModal.value = true
    }

    async function approveWithPayment() {
      const order = selectedOrder.value
      try {
        const { data, error } = await supabase.functions.invoke('create-payment', {
          body: {
            items: order.items.map(item => ({
              ...item,
              price_in_cents: Math.round(item.price * 100)
            })),
            email: order.customer_email,
            user_id: order.user_id,
            shipping_address: order.shipping_address,
            order_id: order.id
          }
        })

        if (error) throw error

        // Copy payment link to clipboard
        const paymentLink = `${window.location.origin}/checkout?session=${data.sessionId}`
        await navigator.clipboard.writeText(paymentLink)
        alert('Payment link copied to clipboard!')

        // Update order status with Stripe session
        await updateOrderStatus(order.id, 'approved', data.sessionId)
      } catch (error) {
        console.error('Error creating payment link:', error)
        alert('Failed to create payment link')
      }
      showModal.value = false
    }

    async function approveWithoutPayment() {
      await updateOrderStatus(selectedOrder.value.id, 'approved')
      showModal.value = false
    }

    async function updateOrderStatus(orderId, status, sessionId = null) {
      const updateData = { status }
      if (sessionId) {
        updateData.stripe_session_id = sessionId
      }

      const { error } = await supabase
        .from('orders')
        .update(updateData)
        .eq('id', orderId)

      if (error) {
        console.error('Error updating order:', error)
        return
      }

      await fetchOrders()
    }

    function calculateSubtotal(order) {
      return order.items.reduce((sum, item) => sum + (item.price * item.quantity), 0)
    }

    function formatDate(date) {
      return new Date(date).toLocaleString('nl-NL', {
        dateStyle: 'medium',
        timeStyle: 'short'
      })
    }

    onMounted(fetchOrders)

    return {
      orders,
      currentStatus,
      filteredOrders,
      updateOrderStatus,
      showModal,
      showApprovalModal,
      approveWithPayment,
      approveWithoutPayment,
      calculateSubtotal,
      formatDate
    }
  }
}
</script>

<style scoped>
.orders-panel {
  padding: 24px;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.panel-header h2 {
  font-size: 24px;
  margin: 0;
}

.status-filters {
  display: flex;
  gap: 8px;
}

.status-filters button {
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  background: var(--surface-background);
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s ease;
}

.status-filters button.active {
  background: var(--accent-primary);
  color: white;
}

.order-card {
  background: var(--surface-background);
  border-radius: 16px;
  border: 1px solid var(--card-border);
  margin-bottom: 16px;
  overflow: hidden;
}

.order-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid var(--card-border);
}

.order-details {
  padding: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr auto;
  gap: 24px;
}

.items-summary ul {
  list-style: none;
  padding: 0;
  margin: 8px 0;
}

.items-summary li {
  padding: 8px 0;
  border-bottom: 1px solid var(--card-border);
}

.status-badge {
  padding: 4px 12px;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 600;
}

.status-badge.pending {
  background: var(--system-yellow);
  color: black;
}

.status-badge.approved {
  background: var(--system-green);
  color: white;
}

.status-badge.rejected {
  background: var(--system-red);
  color: white;
}

.order-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.approve-btn,
.reject-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.approve-btn {
  background: var(--system-green);
  color: white;
}

.reject-btn {
  background: var(--system-red);
  color: white;
}

@media (max-width: 768px) {
  .order-details {
    grid-template-columns: 1fr;
  }
}

.approval-options {
  padding: 24px;
}

.approval-option {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  margin-bottom: 16px;
  border: 1px solid var(--card-border);
  border-radius: 12px;
  background: var(--surface-background);
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

.approval-option:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
  border-color: var(--accent-primary);
}

.approval-option .material-symbols-rounded {
  font-size: 32px;
  color: var(--accent-primary);
}

.approval-option h3 {
  margin: 0 0 4px 0;
  font-size: 16px;
  color: var(--text-primary);
}

.approval-option p {
  margin: 0;
  font-size: 14px;
  color: var(--text-secondary);
}

.modal-actions {
  margin-top: 24px;
  display: flex;
  justify-content: flex-end;
}

.cancel-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  background: var(--surface-background);
  color: var(--text-secondary);
  cursor: pointer;
}

.items-list {
  margin-bottom: 24px;
}

.item-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid var(--card-border);
}

.item-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.item-thumbnail {
  width: 48px;
  height: 48px;
  object-fit: contain;
  border-radius: 8px;
  background: var(--background-elevated);
}

.item-quantity {
  color: var(--text-secondary);
  margin-left: 8px;
}

.item-price {
  font-weight: 600;
  color: var(--accent-primary);
}

.order-totals {
  background: var(--background-elevated);
  border-radius: 12px;
  padding: 16px;
  margin-top: 16px;
}

.total-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  color: var(--text-secondary);
}

.grand-total {
  border-top: 1px solid var(--card-border);
  margin-top: 8px;
  padding-top: 16px;
  color: var(--text-primary);
  font-weight: 600;
  font-size: 18px;
}

.payment-info {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid var(--card-border);
}

.payment-info code {
  display: inline-block;
  padding: 2px 6px;
  background: var(--background-elevated);
  border-radius: 4px;
  font-size: 12px;
  color: var(--accent-primary);
}

.order-info {
  background: var(--surface-background);
  border-radius: 12px;
  padding: 16px;
}

@media (max-width: 768px) {
  .item-info {
    flex: 1;
  }
  
  .item-price {
    min-width: 80px;
    text-align: right;
  }
}
</style>
