import { reactive, watch } from 'vue'

const savedCart = localStorage.getItem('cart')
const initialState = savedCart ? JSON.parse(savedCart) : {
  items: [],
  total: 0,
  show: false
}

const state = reactive(initialState)

watch(() => state.items, (items) => {
  localStorage.setItem('cart', JSON.stringify({
    items,
    total: state.total,
    show: state.show
  }))
}, { deep: true })

export default {
  state,

  toggleCart() {
    state.show = !state.show
  },

  addItem(item, startElement = null) {
    // Animate if we have a start element
    if (startElement) {
      this.animateToCart(startElement)
    }

    // Add or update item
    const existingItem = state.items.find(i => i.id === item.id)
    if (existingItem) {
      existingItem.quantity++
    } else {
      state.items.push({ ...item, quantity: 1 })
    }

    this.calculateTotal()
    state.show = true
  },

  removeItem(itemId) {
    state.items = state.items.filter(item => item.id !== itemId)
    this.calculateTotal()
  },

  updateQuantity(itemId, quantity) {
    const item = state.items.find(i => i.id === itemId)
    if (item) {
      item.quantity = quantity
      this.calculateTotal()
    }
  },

  calculateTotal() {
    state.total = state.items.reduce((sum, item) => {
      return sum + (item.price * item.quantity)
    }, 0)
  },

  clear() {
    state.items = []
    state.total = 0
    state.show = false
  },

  animateToCart(startElement) {
    if (!startElement) return

    const cartIcon = document.querySelector('.cart-button')
    if (!cartIcon) return

    const floatingEl = document.createElement('div')
    const rect = startElement.getBoundingClientRect()
    const cartRect = cartIcon.getBoundingClientRect()

    Object.assign(floatingEl.style, {
      position: 'fixed',
      top: `${rect.top}px`,
      left: `${rect.left}px`,
      width: `${rect.width}px`,
      height: `${rect.height}px`,
      zIndex: 9999,
      pointerEvents: 'none',
      transition: 'all 0.6s cubic-bezier(0.16, 1, 0.3, 1)',
      opacity: '0.8',
      background: 'var(--accent-primary)',
      borderRadius: '12px'
    })

    document.body.appendChild(floatingEl)

    requestAnimationFrame(() => {
      Object.assign(floatingEl.style, {
        transform: `translate(${cartRect.left - rect.left}px, ${cartRect.top - rect.top}px) scale(0.1)`,
        opacity: '0'
      })
    })

    setTimeout(() => floatingEl.remove(), 600)
  }
}
