<template>
  <header class="app-header">
    <nav class="nav-container">
      <!-- Logo and main nav items -->
      <div class="nav-left">
        <router-link to="/" class="logo">
          <span class="material-symbols-rounded">style</span>
          CardWatcher
        </router-link>
        
        <div class="nav-links">
          <router-link to="/collection">Browse Cards</router-link>
          <router-link to="/pricing">Market Prices</router-link>
          <router-link to="/store">Card Market</router-link>
          <!-- Only show these links when logged in -->
          <template v-if="isLoggedIn">
            <router-link to="/my-collection">My Collection</router-link>
            <router-link to="/wishlist">Wishlist</router-link>
          </template>
        </div>
      </div>

      <!-- Auth buttons -->
      <div class="nav-right">
        <!-- Add Cart Button before auth buttons -->
        <button class="nav-button cart-button" @click="toggleCart">
          <span class="material-symbols-rounded">shopping_cart</span>
          <span v-if="cart.state.items.length" class="cart-badge">
            {{ cart.state.items.length }}
          </span>
        </button>

        <template v-if="isLoggedIn">
          <button class="user-menu" @click="toggleUserMenu">
            <span class="material-symbols-rounded">account_circle</span>
            <span class="username">
              {{ userDisplayName }}
              <span class="role-badge" :class="userRole">{{ userRole }}</span>
            </span>
            <span class="material-symbols-rounded">expand_more</span>
          </button>
          <!-- Dropdown menu -->
          <div v-if="showUserMenu" class="user-dropdown">
            <router-link to="/profile" class="dropdown-item">
              <span class="material-symbols-rounded">person</span>
              Profile
            </router-link>
            <router-link to="/admin" class="dropdown-item" v-if="isAdmin">
              <span class="material-symbols-rounded">admin_panel_settings</span>
              Admin Panel
            </router-link>
            <button @click="handleSignOut" class="dropdown-item">
              <span class="material-symbols-rounded">logout</span>
              Sign Out
            </button>
          </div>
        </template>
        <template v-else>
          <router-link to="/register" class="auth-button secondary">Register</router-link>
          <router-link to="/login" class="auth-button primary">
            <span class="material-symbols-rounded">login</span>
            Sign In
          </router-link>
        </template>
      </div>
    </nav>

    <!-- Add Cart SlideOver -->
    <CartSlideOver :show="showCart" @close="showCart = false" />
  </header>
</template>

<script>
import auth from '@/store/auth'
import cart from '@/store/cart'
import { ref, computed, watch } from 'vue'
import CartSlideOver from './CartSlideOver.vue'
import { useRouter } from 'vue-router'

export default {
  name: 'AppHeader',
  components: {
    CartSlideOver
  },
  setup() {
    const showUserMenu = ref(false)

    const isLoggedIn = computed(() => !!auth.state.session)
    const userDisplayName = computed(() => {
      if (!auth.state.user) return ''
      const profile = auth.state.profile
      if (profile && profile.first_name && profile.last_name) {
        return `${profile.first_name} ${profile.last_name}`
      }
      return auth.state.user.email
    })

    const isAdmin = computed(() => auth.state.isAdmin)

    const userRole = computed(() => {
      return auth.state.isAdmin ? 'admin' : 'user'
    })

    const toggleUserMenu = () => {
      showUserMenu.value = !showUserMenu.value
    }

    const handleSignOut = async () => {
      await auth.signOut()
      showUserMenu.value = false
    }

    const showCart = ref(false)
    const router = useRouter()

    function toggleCart() {
      router.push('/cart')
    }

    // Watch cart show state
    watch(() => cart.state.show, (newValue) => {
      showCart.value = newValue
    })

    // Watch cart items for badge animation
    watch(() => cart.state.items.length, (newValue, oldValue) => {
      if (newValue > oldValue) {
        const badge = document.querySelector('.cart-badge')
        if (badge) {
          badge.classList.add('pulse')
          setTimeout(() => badge.classList.remove('pulse'), 300)
        }
      }
    })

    return {
      isLoggedIn,
      showUserMenu,
      toggleUserMenu,
      handleSignOut,
      userDisplayName,
      isAdmin,
      userRole,
      cart,
      showCart,
      toggleCart
    }
  }
}
</script>

<style scoped>
.app-header {
  background: var(--card-background);
  backdrop-filter: var(--blur-md);
  -webkit-backdrop-filter: var(--blur-md);
  border-bottom: 1px solid var(--card-border);
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 16px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-left {
  display: flex;
  align-items: center;
  gap: 48px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
  text-decoration: none;
}

.nav-links {
  display: flex;
  gap: 24px;
}

.nav-links a {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.nav-links a:hover {
  color: var(--text-primary);
}

.nav-links a.router-link-active {
  color: var(--accent-primary);
}

.nav-right {
  position: relative;
  display: flex;
  align-items: center;
  gap: 12px;
}

.auth-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border-radius: 8px;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s ease;
}

.auth-button.primary {
  background: var(--accent-primary);
  color: white;
}

.auth-button.secondary {
  background: var(--surface-background);
  color: var(--text-primary);
}

.user-menu {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: var(--surface-background);
  border: none;
  border-radius: 8px;
  color: var(--text-primary);
  cursor: pointer;
}

.user-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 8px;
  background: var(--card-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  padding: 8px;
  min-width: 200px;
  box-shadow: var(--shadow-lg);
}

.dropdown-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  width: 100%;
  border: none;
  background: none;
  color: var(--text-primary);
  font-size: 14px;
  text-decoration: none;
  cursor: pointer;
  border-radius: 6px;
}

.dropdown-item:hover {
  background: var(--surface-background);
}

.role-badge {
  display: inline-block;
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  margin-left: 8px;
}

.role-badge.admin {
  background: var(--system-red);
  color: white;
}

.role-badge.user {
  background: var(--system-blue);
  color: white;
}

.cart-button {
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: none;
  background: var(--surface-background);
  color: var(--text-primary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.cart-button:hover {
  background: var(--background-elevated);
  transform: translateY(-2px);
}

.cart-badge {
  position: absolute;
  top: -6px;
  right: -6px;
  background: var(--accent-primary);
  color: white;
  font-size: 12px;
  font-weight: 600;
  padding: 2px 6px;
  border-radius: 10px;
  min-width: 18px;
  text-align: center;
  animation: none;
}

.cart-badge.pulse {
  animation: pulse 0.3s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.5); }
  100% { transform: scale(1); }
}

/* Ensure cart slide-over appears above header */
:deep(.cart-overlay) {
  z-index: var(--z-index-cart);
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }
  
  .username {
    display: none;
  }
}
</style>
