<template>
  <div class="store">
    <!-- Hero Section -->
    <div class="store-hero">
      <div class="hero-content">
        <h1>Card Market</h1>
        <p class="subtitle">Discover rare and valuable Pokémon cards</p>
      </div>
      <div class="quick-categories">
        <button 
          v-for="category in categories" 
          :key="category.id"
          class="category-pill"
          :class="{ active: filters.category === category.id }"
          @click="filters.category = category.id"
        >
          <span class="material-symbols-rounded">{{ category.icon }}</span>
          {{ category.name }}
        </button>
      </div>
    </div>

    <!-- Enhanced Filter Section -->
    <div class="filter-section">
      <div class="search-container">
        <div class="search-box">
          <span class="material-symbols-rounded">search</span>
          <input 
            v-model="search" 
            type="text" 
            placeholder="Search for cards, sets, or boosters..."
          >
        </div>
        <div class="advanced-filters">
          <select v-model="filters.priceRange">
            <option value="">All Prices</option>
            <option value="under10">Under $10</option>
            <option value="10to50">$10 - $50</option>
            <option value="50plus">$50+</option>
          </select>
          <select v-model="filters.rarity">
            <option value="">All Rarities</option>
            <option value="common">Common</option>
            <option value="uncommon">Uncommon</option>
            <option value="rare">Rare</option>
            <option value="ultra">Ultra Rare</option>
          </select>
          <select v-model="filters.condition">
            <option value="">All Conditions</option>
            <option value="mint">Mint</option>
            <option value="near_mint">Near Mint</option>
            <option value="excellent">Excellent</option>
            <option value="good">Good</option>
          </select>
        </div>
      </div>
    </div>

    <!-- Active Filters -->
    <div v-if="activeFiltersCount" class="active-filters">
      <span class="filter-tag" v-if="filters.priceRange" @click="filters.priceRange = ''">
        {{ formatPriceRange(filters.priceRange) }}
        <span class="material-symbols-rounded">close</span>
      </span>
      <span class="filter-tag" v-if="filters.rarity" @click="filters.rarity = ''">
        {{ filters.rarity }}
        <span class="material-symbols-rounded">close</span>
      </span>
      <span class="filter-tag" v-if="filters.condition" @click="filters.condition = ''">
        {{ filters.condition }}
        <span class="material-symbols-rounded">close</span>
      </span>
      <button class="clear-filters" @click="clearFilters">Clear All</button>
    </div>

    <!-- Enhanced Product Grid -->
    <div class="products-grid">
      <TransitionGroup name="product-card">
        <div v-for="product in paginatedProducts" 
             :key="product.id" 
             class="product-card"
             :class="{ 
               'out-of-stock': product.stock <= 0,
               'in-wishlist': isInWishlist(product.id)
             }"
        >
          <div class="product-image">
            <img :src="product.image_url" :alt="product.name">
            <div class="product-badges" v-if="product.metadata">
              <span v-if="product.metadata.rarity" class="rarity-badge">
                {{ product.metadata.rarity }}
              </span>
              <span v-if="product.stock < 5 && product.stock > 0" class="stock-badge">
                Low Stock
              </span>
            </div>
          </div>
          <div class="product-info">
            <div class="product-header">
              <span class="product-type">{{ product.type }}</span>
              <h3>{{ product.name }}</h3>
            </div>
            <div class="product-meta">
              <span class="product-set" v-if="product.metadata && product.metadata.set">
                {{ product.metadata.set.name }}
              </span>
              <span class="product-price">${{ product.price }}</span>
            </div>
            <button 
              class="buy-button"
              :disabled="product.stock <= 0"
              @click="addToCart(product, $event)"
            >
              {{ product.stock <= 0 ? 'Out of Stock' : 'Add to Cart' }}
            </button>
          </div>
          <div class="product-actions">
            <button 
              class="wishlist-button"
              :class="{ active: isInWishlist(product.id) }"
              @click.stop="toggleWishlist(product)"
            >
              <span class="material-symbols-rounded">
                {{ isInWishlist(product.id) ? 'favorite' : 'favorite_border' }}
              </span>
            </button>
            <button 
              class="quick-view-button"
              @click.stop="showQuickView(product)"
            >
              <span class="material-symbols-rounded">visibility</span>
            </button>
          </div>
        </div>
      </TransitionGroup>
    </div>

    <!-- Pagination -->
    <div v-if="totalPages > 1" class="pagination">
      <button 
        :disabled="currentPage === 1"
        @click="currentPage--"
      >
        <span class="material-symbols-rounded">chevron_left</span>
      </button>
      <button 
        v-for="page in displayedPages" 
        :key="page"
        :class="{ active: currentPage === page }"
        @click="currentPage = page"
      >
        {{ page }}
      </button>
      <button 
        :disabled="currentPage === totalPages"
        @click="currentPage++"
      >
        <span class="material-symbols-rounded">chevron_right</span>
      </button>
    </div>

    <!-- Quick View Modal -->
    <Modal v-if="quickViewProduct" @close="quickViewProduct = null">
      <template #default>
        <div class="quick-view">
          <div class="quick-view-image">
            <img :src="quickViewProduct.image_url" :alt="quickViewProduct.name">
          </div>
          <div class="quick-view-details">
            <h2>{{ quickViewProduct.name }}</h2>
            <div class="meta-info">
              <span class="set" v-if="quickViewProduct.metadata && quickViewProduct.metadata.set">
                {{ quickViewProduct.metadata.set.name }}
              </span>
              <span class="rarity" v-if="quickViewProduct.metadata">
                {{ quickViewProduct.metadata.rarity }}
              </span>
            </div>
            <div class="pricing">
              <div class="market-price">
                <span>Market Price</span>
                <strong>${{ getMarketPrice(quickViewProduct) }}</strong>
              </div>
              <div class="our-price">
                <span>Our Price</span>
                <strong>${{ quickViewProduct.price }}</strong>
              </div>
            </div>
            <div class="stock-info">
              <span class="material-symbols-rounded">
                {{ quickViewProduct.stock > 0 ? 'inventory_2' : 'inventory_2_off' }}
              </span>
              {{ quickViewProduct.stock > 0 ? `${quickViewProduct.stock} in stock` : 'Out of stock' }}
            </div>
            <button 
              class="add-to-cart"
              :disabled="quickViewProduct.stock <= 0"
              @click="addToCart(quickViewProduct, $event)"
            >
              Add to Cart
            </button>
          </div>
        </div>
      </template>
    </Modal>

    <!-- Filters Modal -->
    <Modal v-if="showFilters" @close="showFilters = false">
      <template #header>
        <h2>Filter Products</h2>
      </template>
      <template #default>
        <div class="filters-form">
          <!-- ... existing filter form ... -->
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { supabase } from '@/services/supabase'
import Modal from '@/components/Modal.vue'
import auth from '@/store/auth'
import CartSlideOver from '@/components/CartSlideOver.vue'
import QuickPreview from '@/components/QuickPreview.vue'
import cart from '@/store/cart'

export default {
  name: 'Store',
  components: { 
    Modal, 
    CartSlideOver,
    QuickPreview
  },
  setup() {
    const search = ref('')
    const showSellModal = ref(false)
    const showBuyModal = ref(false)
    const showFilters = ref(false)
    const selectedListing = ref(null)
    const listings = ref([])
    const filters = ref({
      condition: '',
      sort: 'newest',
      category: '',
      priceRange: '',
      rarity: ''
    })
    const currentPage = ref(1)
    const itemsPerPage = 12
    const quickViewProduct = ref(null)
    const categories = ref([
      { id: 'all', name: 'All', icon: 'category' },
      { id: 'rare', name: 'Rare', icon: 'star' },
      { id: 'common', name: 'Common', icon: 'circle' },
      { id: 'uncommon', name: 'Uncommon', icon: 'diamond' }
    ])

    // Add new refs for products and loading state
    const products = ref([])
    const isLoading = ref(true)

    // Create computed properties
    const currentUserId = computed(() => {
      return auth.state.user ? auth.state.user.id : null
    })

    const activeFiltersCount = computed(() => 
      Object.values(filters.value).filter(Boolean).length
    )

    // Modified computed property for filtered products
    const filteredProducts = computed(() => {
      let result = [...products.value]

      if (search.value) {
        const searchTerm = search.value.toLowerCase()
        result = result.filter(p => 
          p.name.toLowerCase().includes(searchTerm)
        )
      }

      // Apply category filter
      if (filters.value.category && filters.value.category !== 'all') {
        result = result.filter(p => p.type === filters.value.category)
      }

      // Apply filters
      if (filters.value.priceRange) {
        result = applyPriceFilter(result, filters.value.priceRange)
      }
      if (filters.value.rarity) {
        result = result.filter(p => 
          p.metadata && 
          p.metadata.rarity && 
          p.metadata.rarity.toLowerCase() === filters.value.rarity
        )
      }
      if (filters.value.condition) {
        result = result.filter(p => 
          p.metadata && 
          p.metadata.condition && 
          p.metadata.condition.toLowerCase() === filters.value.condition
        )
      }

      // Apply sort
      return sortProducts(result, filters.value.sort)
    })

    const paginatedProducts = computed(() => {
      const start = (currentPage.value - 1) * itemsPerPage
      return filteredProducts.value.slice(start, start + itemsPerPage)
    })

    const totalPages = computed(() => 
      Math.ceil(filteredProducts.value.length / itemsPerPage)
    )

    const displayedPages = computed(() => {
      // Logic to show limited page numbers with ellipsis
      const total = totalPages.value
      const current = currentPage.value
      const delta = 2
      const range = []
      const rangeWithDots = []
      let l

      range.push(1)
      for (let i = current - delta; i <= current + delta; i++) {
        if (i >= 1 && i <= total) {
          range.push(i)
        }
      }
      range.push(total)

      range.forEach((i) => {
        if (l) {
          if (i - l === 2) {
            rangeWithDots.push(l + 1)
          } else if (i - l !== 1) {
            rangeWithDots.push('...')
          }
        }
        rangeWithDots.push(i)
        l = i
      })

      return rangeWithDots
    })

    // Fetch listings
    async function fetchListings() {
      const { data, error } = await supabase
        .from('products')
        .select(`*`)
        .eq('active', true)

      if (error) {
        console.error('Error fetching listings:', error)
        return
      }

      listings.value = data || []
    }

    // Modified fetch function
    async function fetchProducts() {
      console.log('Fetching products...');
      isLoading.value = true;
      try {
        const { data, error } = await supabase
          .from('products')
          .select('*')
          .eq('active', true)
          .order('created_at', { ascending: false });

        if (error) {
          console.error('Supabase error:', error);
          throw error;
        }

        console.log('Raw data from Supabase:', data);

        if (!data || data.length === 0) {
          console.log('No products found in database');
          products.value = [];
          return;
        }

        products.value = data.map(product => ({
          ...product,
          price: parseFloat(product.price) || 0,
          stock: parseInt(product.stock) || 0
        }));

        console.log('Transformed products:', products.value);
      } catch (error) {
        console.error('Error fetching products:', error);
      } finally {
        isLoading.value = false;
      }
    }

    function formatCondition(condition) {
      return condition.replace('_', ' ').replace(/\b\w/g, l => l.toUpperCase())
    }

    function buyCard(listing) {
      selectedListing.value = listing
      showBuyModal.value = true
    }

    async function confirmPurchase() {
      if (!selectedListing.value || !auth.state.user) return

      try {
        // Create order
        const { error: orderError } = await supabase
          .from('orders')
          .insert({
            product_id: selectedListing.value.id,
            user_id: auth.state.user.id,
            quantity: 1,
            total_amount: selectedListing.value.price,
            status: 'completed'
          })

        if (orderError) throw orderError

        // Update product status and stock
        const { error: productError } = await supabase
          .from('products')
          .update({ 
            active: false,
            stock: selectedListing.value.stock - 1 
          })
          .eq('id', selectedListing.value.id)

        if (productError) throw productError

        showBuyModal.value = false
        await fetchListings()
      } catch (error) {
        console.error('Error processing purchase:', error)
      }
    }

    const selectedProduct = ref(null)
    const showPreview = ref(false)

    function showQuickPreview(product) {
      selectedProduct.value = product
      showPreview.value = true
    }

    function clearFilters() {
      filters.value = {
        priceRange: '',
        rarity: '',
        condition: '',
        sort: 'newest',
        category: ''
      }
      currentPage.value = 1
    }

    function showQuickView(product) {
      quickViewProduct.value = product;
    }

    function applyPriceFilter(products, range) {
      switch (range) {
        case 'under10':
          return products.filter(p => p.price < 10)
        case '10to50':
          return products.filter(p => p.price >= 10 && p.price <= 50)
        case '50plus':
          return products.filter(p => p.price > 50)
        default:
          return products
      }
    }

    function sortProducts(products, sortBy) {
      switch (sortBy) {
        case 'price_low':
          return products.sort((a, b) => a.price - b.price)
        case 'price_high':
          return products.sort((a, b) => b.price - a.price)
        case 'newest':
          return products.sort((a, b) => new Date(b.created_at) - new Date(a.created_at))
        case 'popularity':
          return products.sort((a, b) => b.popularity - a.popularity)
        default:
          return products
      }
    }

    // Add wishlist functionality
    const wishlist = ref([])

    // Modify fetch wishlist function
    async function fetchUserWishlist() {
      if (!auth.state.user) return;

      try {
        const { data, error } = await supabase
          .from('wishlists') // Changed from 'wishlist' to 'wishlists'
          .select('card_id')
          .eq('user_id', auth.state.user.id);

        if (error) throw error;

        wishlist.value = data.map(item => item.card_id);
        console.log('Fetched wishlist:', wishlist.value);
      } catch (error) {
        console.error('Error fetching wishlist:', error);
      }
    }

    // Replace existing isInWishlist function
    function isInWishlist(productId) {
      return wishlist.value.includes(productId);
    }

    // Update toggleWishlist function
    async function toggleWishlist(product) {
      if (!auth.state.user) {
        alert('Please log in to add items to your wishlist');
        return;
      }

      try {
        const isInList = isInWishlist(product.id);
        
        if (isInList) {
          // Remove from wishlist
          const { error } = await supabase
            .from('wishlists') // Changed from 'wishlist' to 'wishlists'
            .delete()
            .eq('user_id', auth.state.user.id)
            .eq('card_id', product.id);

          if (error) throw error;
          wishlist.value = wishlist.value.filter(id => id !== product.id);
        } else {
          // Add to wishlist with card data
          const { error } = await supabase
            .from('wishlists') // Changed from 'wishlist' to 'wishlists'
            .insert({
              user_id: auth.state.user.id,
              card_id: product.id,
              card_data: {
                name: product.name,
                image_url: product.image_url,
                price: product.price,
                type: product.type,
                metadata: product.metadata
              }
            });

          if (error) throw error;
          wishlist.value.push(product.id);
        }
      } catch (error) {
        console.error('Error updating wishlist:', error);
        alert('Failed to update wishlist');
      }
    }

    // Fix market price getter to avoid optional chaining
    function getMarketPrice(product) {
      if (!product.metadata) return '0.00';
      
      let marketPrice = product.price; // default to product price

      if (product.metadata.cardmarket && 
          product.metadata.cardmarket.prices && 
          product.metadata.cardmarket.prices.averageSellPrice) {
        marketPrice = product.metadata.cardmarket.prices.averageSellPrice;
      } else if (product.metadata.tcgplayer && 
                 product.metadata.tcgplayer.prices && 
                 product.metadata.tcgplayer.prices.market) {
        marketPrice = product.metadata.tcgplayer.prices.market;
      }
      
      return parseFloat(marketPrice).toFixed(2);
    }

    // Add to cart functionality
    function addToCart(product, event) {
      const targetElement = event && event.target ? event.target : null;
      if (!product.stock || product.stock <= 0) {
        alert('This product is out of stock');
        return;
      }

      cart.addItem({
        id: product.id,
        name: product.name,
        price: product.price,
        image_url: product.image_url,
        quantity: 1
      }, targetElement);

      // Close quick view modal if open
      quickViewProduct.value = null;
    }

    // Initialize
    onMounted(async () => {
      await Promise.all([
        fetchProducts(),
        auth.state.user ? fetchUserWishlist() : Promise.resolve()
      ]);
    });

    return {
      search,
      filters,
      filteredProducts,
      showSellModal,
      showBuyModal,
      showFilters,
      selectedListing,
      buyCard,
      confirmPurchase,
      formatCondition,
      currentUserId,
      categories,
      selectedProduct,
      showPreview,
      showQuickPreview,
      cart,
      currentPage,
      quickViewProduct,
      activeFiltersCount,
      paginatedProducts,
      totalPages,
      displayedPages,
      clearFilters,
      showQuickView,
      products,
      isLoading,
      isInWishlist,
      toggleWishlist,
      fetchProducts, // Export for debugging
      wishlist,
      getMarketPrice,
      addToCart
    }
  }
}
</script>

<style scoped>
.store {
  max-width: 1440px;
  margin: 0 auto;
  padding: 24px;
}

.store-hero {
  text-align: center;
  padding: 80px 24px;
  margin: -24px -24px 32px;
  background: linear-gradient(
    to bottom,
    var(--background-primary),
    var(--background-elevated)
  );
}

.hero-content h1 {
  font-size: 56px;
  line-height: 1.1;
  font-weight: 700;
  letter-spacing: -0.015em;
  margin: 0;
  background: linear-gradient(135deg, var(--text-primary), var(--text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-size: 24px;
  line-height: 1.3;
  color: var(--text-secondary);
  margin: 16px 0 0;
  font-weight: 500;
}

.quick-categories {
  display: flex;
  gap: 12px;
  justify-content: center;
  flex-wrap: wrap;
}

.category-pill {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 100px;
  border: none;
  background: var(--surface-background);
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.category-pill:hover {
  background: var(--background-elevated);
  transform: translateY(-1px);
}

.category-pill.active {
  background: var(--accent-primary);
  color: white;
}

.search-container {
  position: sticky;
  top: 80px;
  z-index: 90;
  background: var(--background-primary);
  padding: 24px 0;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--card-border);
  margin-bottom: 32px;
}

.search-box {
  background: var(--surface-background);
  border-radius: 16px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
  border: 1px solid var(--card-border);
  transition: all 0.3s ease;
}

.search-box:focus-within {
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 4px var(--accent-primary-alpha);
}

.search-box input {
  width: 100%;
  border: none;
  background: none;
  font-size: 16px;
  color: var(--text-primary);
  outline: none;
}

.filter-pills {
  display: flex;
  gap: 12px;
}

.filter-select {
  padding: 10px 16px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
  min-width: 180px;
  appearance: none;
}

.filter-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 12px;
  border: 1px solid var(--card-border);
  background: var(--surface-background);
  color: var(--text-primary);
  font-size: 14px;
  cursor: pointer;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 24px;
  margin-top: 32px;
}

.product-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.16, 1, 0.3, 1);
  height: 500px; /* Add fixed height */
  display: flex;
  flex-direction: column;
}

.product-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
  border-color: var(--accent-primary);
}

.product-image {
  position: relative;
  width: 100%;
  height: 300px; /* Fixed height for image container */
  background: linear-gradient(135deg, rgba(255,255,255,0.05), rgba(255,255,255,0.01));
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  overflow: hidden;
}

.product-image img {
  width: auto;
  height: 100%;
  max-width: 100%;
  object-fit: contain;
  transition: transform 0.6s cubic-bezier(0.16, 1, 0.3, 1);
}

.product-info {
  flex: 1;
  padding: 24px;
  background: linear-gradient(to bottom, transparent, rgba(0,0,0,0.1));
  display: flex;
  flex-direction: column;
}

.product-header {
  margin-bottom: 12px;
}

.product-header h3 {
  font-size: 18px;
  line-height: 1.4;
  font-weight: 600;
  margin: 8px 0;
  background: linear-gradient(135deg, var(--text-primary), var(--text-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.product-meta {
  margin-top: auto; /* Push to bottom of flex container */
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.buy-button {
  width: 100%;
  padding: 14px 24px;
  border: none;
  border-radius: 12px;
  background: var(--accent-primary);
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  margin-top: 16px;
